<template>
  <div>
    <heads :head='headData'></heads>

    <aside class="article">
    <div class="center">
    <div class="reg-left">
    <h3>请使用手机号注册</h3>
    <form @submit.prevent="">
      <div class="form-item">
        <label class="label">手 机 号 码：</label><input class="regInput" v-model="reg.mobile" placeholder="仅限大陆手机号" maxlength="11" autocomplete="off">
        <span class="animated" :class="classMsg" v-show="reg.mobile.length==11"><i :class="{'el-icon-circle-close color-no':!!msg,'el-icon-circle-check color-ok':!msg}" :title="msg"></i></span>
      </div>

      <div class="form-item btn-input-w">
        <label class="label">手机动态码：</label><input class="regInput btn-code-w" v-model="reg.verify" placeholder="短信验证码">
        <button :disabled="mVerify || codeNo" class="btn-phonecode" @click='postCode'>{{send}}</button>
        <span class="animated code-no" :class="classMsg" v-if="error.mobile"><i class="el-icon-circle-close color-no" :title="error.mobile.message"></i></span>
      </div>

      <div class="form-item">
        <label class="label">设 置 密 码：</label><input minlength="6" class="regInput" v-model="reg.password" placeholder="不少于 6 位" type="password">
        <span class="animated" :class="classMsg" v-if="error.password"><i class="el-icon-circle-close color-no" :title="error.password.message"></i></span>
        <span class="animated" :class="classMsg" v-if="passConf"><i class="el-icon-circle-check color-ok" title="OK"></i></span>
      </div>

      <div class="form-item animated" :class="classMsg" v-if="passConf">
        <label class="label">确 认 密 码：</label><input class="regInput" v-model="reg.password_confirmation" placeholder="请确认密码" type="password">
        <span class="animated"  :class="classMsg" v-if="confirmation"><i class="el-icon-circle-close color-no" title="密码不一至！"></i></span>
        <span class="animated"  :class="classMsg" v-if="!confirmation"><i class="el-icon-circle-check color-ok" title="OK"></i></span>
      </div>

      <div class="reg-xieyi"><el-checkbox v-model="checked">我已阅读并接受</el-checkbox> <span class="reg-xieyi-c">《联塑云商用户服务协议》</span></div>
      
      <div class="form-item btn-reg">
        <button :disabled="!!mVerify || !!!reg.verify || !!!reg.password || !!confirmation || !checked || loginText" class="btn-register" type="submit" @click="regs"><label v-if="!loginText">注册</label><label v-if="loginText">正在注册...</label></button>
      </div>
      
    </form>
    </div>
    <div class="reg-right">
      <div class="reg-right-text">

        <h4>使用合作第三方账号登录</h4>
        
        <ul>
            <li>
              <div class="login-icon reg-right-img-wb"></div>
              微博
            </li>

            <li>
             <div class="login-icon reg-right-img-qq"></div>
             QQ
            </li>

            <li>
             <div class="login-icon reg-right-img-wx"></div>
             微信
            </li>

        </ul>

        <div>
          <h4>注册之后你可以</h4>
          <p>购买商品支付订单</p>
          <p>收藏商品收藏店铺</p>
          <p>安全交易诚信无忧</p>
          <p>会员等级享受特权</p>
          <p>交易评价站外分享</p>
        </div>
      </div>
    </div>
    </div>
    </aside>

  <Foot></Foot>
  </div>
  

</template>

<script>
/* eslint-disable */
import Heads from '../../components/header'
import Foot from '../../components/footer'
import 'normalize.css'
import Vue from 'vue'
import VueResource from 'vue-resource'
import auths from '../../components/auth'
import { Checkbox, Icon } from 'element-ui'

Vue.component(Checkbox.name, Checkbox)
Vue.component(Icon.name, Icon)
Vue.use(VueResource)

export default {
  components: {
      Heads,
      Foot
  },
  data () {
    return {
      headData:{
        h:'登录',
        text:'已经注册，现在就去',
        url:'/member/login'
      },
      precheckUrl:'http://api.lessoald.cn/v1/auth/register/mobile/precheck',//检测手机是否可以注册
      regUrl: 'http://api.lessoald.cn/v1/auth/register',//新用户注册
      apiURLCode:'http://api.lessoald.cn/v1/auth/register/code',//获取手机验证码
      header:{ 
        headers:{ 
          Accept:'application/x.lessocloud.v1+json' 
        }
      },
      reg:{
        mobile: '',
        verify: '',
        //username:'',
        password:'',
        password_confirmation:''
      },
      msg:'',
      send:'获取验证码',
      codeNo:true,
      error: '',
      checked: false,
      loginText: false
    }
  },
  computed: {
      confirmation () {
        let t = this
        return t.reg.password === t.reg.password_confirmation ?  false :  true
      },
      passConf(){
        let t = this
        return t.reg.password.length >= 6 ? true : false
      },
      classMsg(){
        let t = this
        return {fadeInIn: t.confirmation, fadeInOut: !t.confirmation}
      },
      mVerify(){
        let t = this
        return t.reg.mobile.length < 11 ? true : false
      }
  },
  watch: {
    'reg.mobile': {
      handler: function (val, oldVal) {
        if (this.reg.mobile.length == 11) {
          this.codeNos()
        }
      },
      deep:true
    }
  },
  methods: {
    regs () {//注册
      let vm = this,newdata = '?'+parseInt(new Date().getTime())
      vm.error = ''
      vm.loginText = true
      this.$http.post(this.regUrl+newdata, JSON.stringify(this.reg) , this.header)
      .then((res) => {
        vm.loginText = false
        if (res.body.status === 1) {
          auths.setItem('token', 'Bearer '+res.body.data.token, 365, null ,null ,null )
          auths.setItem('mobile', vm.reg.mobile, 365, null ,null ,null )
          if ( auths.hasItem('token') ) {
            window.location.href="/member/login"
          }
          return
        }else if (res.body.status === 0) {
          vm.error = res.body.error
          //console.log(JSON.stringify(res.body.error))
          return
        }
        
      }, (res) => { 
        vm.loginText = false
      });
    },
    codeNos(){
      let vm = this
      vm.codeNo = true
      this.$http.post(vm.precheckUrl, { mobile:+vm.reg.mobile }, vm.header).then(
        (res) => {
          let codeMsg = res.body
          if (codeMsg.result === true) {
            vm.codeNo = false 
            vm.msg = ''
            return
          }else if (codeMsg.result === false) {
            vm.codeNo = true 
            vm.msg = '该手机号码已注册'
            return
          }else if (codeMsg.status === 0) {
            vm.codeNo = true 
            vm.msg = '请输入11位大陆手机号'
            return
          }
       }, 
       (res) => {
          vm.codeNo = true
          vm.msg = '出错了'
       });
    },
    postCode() {//验证码
       var vm = this
       vm.codeNo = true;
       vm.error = ''
       this.$http.post(this.apiURLCode, { mobile:+this.reg.mobile }, this.header)
       .then((res) => { 
        let resMsg = res.body
        if (resMsg.status === 1) {
          let t = resMsg.data.interval;
          let setIntervalData =  setInterval(function() {
                if (t > 0) {
                    vm.codeNo = true;
                    vm.send = '重新发送('+ t-- +')';
                } else {
                    clearInterval(setIntervalData);
                    vm.codeNo = false;
                    vm.send = '重新发送';
                }
          }, 1000);

          vm.reg.verify = res.body.data.code
        }
      }, (res) => { });    
    }
  }
}
</script>
<style type="text/css">
  .article{
    width: 100%;
    height: 580px;
    background-color: #f2f2f2;
    display: table;
    content: " ";
  }

  .article .center {
    width: 1020px;
    margin: 0 auto;
  }

  .article .center h3 {
    font-size: 28px;
    font-weight: inherit;
    color: #ff6600;
    margin: 42px auto;
  }

  .form-item{
    position: relative;
    border: solid 1px #d6dce0;
    width: 420px;
    height: 46px;
    z-index: 0;
    margin-bottom: 20px;
    background-color: #fff;
    border-radius: 2px;
  }

  .form-item .label{
    font-size: 16px;
    margin-left:15px;
    color: #313234;
  }

  .form-item span{
    position: absolute;
    top: 15px;
    right: -28px;
  }
  .form-item .code-no{
    right: -168px;
  }

  .form-item .regInput {
    border: 0 none;
    font-size: 16px;
    width: 190px;
    height: 16px;
    padding-bottom: 11px;
    padding-left: 12px;
    padding-top: 14px;
    outline: 0;

  }

  .btn-reg{
    height: 48px;
    border: none;;
  }

  .btn-register {
    width: 100%;
    height: 48px;
    color: #fff;
    background: #ff6600;
    border: 0;
    font-size: 20px;
    outline: 0;
    border-radius: 2px;
  }
  .btn-input-w{
    width: 280px;
  }
  .form-item .btn-code-w{
    width: 150px;
  }
  .btn-phonecode {
    position: absolute;
    right: -140px;
    top: -1px;
    width: 133px;
    height: 48px;
    border: none;
    background: #ff6600;
    color: #fff;
    padding: 0;
    outline: 0;
    border-radius: 2px;
  }
  .reg-xieyi{
    position: relative;
    font-size: 14px;
    margin-bottom: 17px;
    color: #313234;

  }
  .reg-xieyi-c{
    color:#007eff;
  }
  button[disabled],button:disabled{
    background-color: rgba(255,179,128,1);
    color: #fff;
    border-radius: 2px;
  }
  .reg-left{
    float: left;
  }

  .reg-right{
    float: left;
    width: 500px;
    height: 480px;
    margin: 50px 0 0 90px;
    border-left: 1px solid #ccc;
  }
  .reg-right-text{
    width: 410px;
    height: 350px;
    background-color: #fff;
    border-radius: 2px;
    float: right;
    margin-top: 65px;
    text-align: center;
    color: #313234;

  }
  .reg-right-text ul{
    padding: 0;
  }
  .reg-right-text li{
    list-style-type: none;
    display: inline-block;
    margin: 6px;
    font-size: 14px;
  }
  .reg-right .login-icon{
    width: 45px;
    height: 45px;
  }
  .reg-right p{
    line-height: 12px;
  }
  .reg-right .reg-right-img-wb{
    background-position: 4px -62px;
  }
  .reg-right .reg-right-img-qq{
    background-position: 7px -110px;
  }
  .reg-right .reg-right-img-wx{
    background-position: 2px -161px;
  }
  .reg-right-text h4{
    color: #acb3b7;
    font-weight: normal;
  }

  .btn-register,.regInput,.btn-phonecode{
    font-family: "Microsoft YaHei","Hiragino Sans GB";
  }
</style>


